
import pubfooter from '@/components/pubfooter/pubfooter'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import axios from 'axios'
import tools from '@/common/tools.js'

export default {
name: 'shopcart',
data: function() {
return {
allData: [],
show: false,
confirmShow: false,
delNum: 0,
checkArr: [],
checkAllStatus: false,
isDelSelect: false,
delIdArr: [],
allTotalPrice: 0,
checkAllNum: 0
}
},
watch: {
allData: {
handler: function(newval, oldVal) {
console.log('new: %s, old: %s', newval, oldVal)
this.countPrice()
this.sendPhp()
},
deep: true
}
},
directives: {
TransferDom
},
created() {
this._getShopCartData()
},
activated() {
this._getShopCartData()
},
methods: {
jiesuan() {
if (this.allTotalPrice == 0) {
tools.showError('请先选择商品')
} else {
this.$router.push({path: 'confirmorder'})
}
},
sendPhp() {
let url = '/Mobile/Cart/AsyncUpdateCart.html'
axios.post(url, {
'cart': this.allData
})
.then((res) => {
console.log(res)
if (res.data.status === 1) {



}
})
.catch((error) => {
console.log(error)
})
},
addNumBtn(index) {
console.log(index, this.allData[index].goods_num)
this.allData[index].goods_num = parseInt(this.allData[index].goods_num) + 1
},
delNumBtn(index) {
console.log(index, this.allData[index].goods_num)
this.allData[index].goods_num = parseInt(this.allData[index].goods_num) - 1
},
countPrice() {
this.allTotalPrice = 0
this.checkAllNum = 0
this.allData.forEach((item) => {
if (item.invalid !== 0 && item.selected == '1') {
this.allTotalPrice = this.allTotalPrice + parseFloat(item.goods_price) * parseFloat(item.goods_num)
this.checkAllNum++
}
})
},
oneCheck(index) {

if (this.allData[index].selected === '1') {
this.allData[index].selected = '0'
} else {
this.allData[index].selected = '1'
}
console.log('我的值是', this.allData[index].selected)
},
checkAll() {
console.log('全选')
console.log(this.checkAllStatus)
this.allData.forEach((item, i) => {
if (item.invalid !== 0) {
item.selected = this.checkAllStatus ? '0' : '1'
}
})
},
onCancel() {
this.delIdArr = []
},
onConfirm() {
console.log('我要开始删除了')
/* let delIdArr = []
if (this.isDelSelect) {
this.allData.forEach((item) => {
if (item.invalid !== 0 && item.selected === '1') {
delIdArr.push(item.id)
}
})
} else {
delIdArr.push(this.allData[this.delNum].id)
} */
console.log('delIdArr值是', this.delIdArr)
let url = '/Mobile/Cart/delete.html'
axios.post(url, {
cart_ids: this.delIdArr
})
.then((res) => {
console.log(res)
if (res.data.status === 1) {

this.delIdArr.forEach((item) => {
console.log(item)
this.allData.forEach((m, index) => {
if (item === m.id) {
this.allData.splice(index, 1)
}
})
})
tools.showError(res.data.msg)
this.delIdArr = []
}
})
.catch((error) => {
console.log(error)
})
},
delShop(index) {
console.log(index)
console.log(this.allData[index].id)
this.isDelSelect = false
this.confirmShow = true
this.delNum = index
this.delIdArr.push(this.allData[this.delNum].id)
},
delSelect() {
this.allData.forEach((item) => {
console.log(item)
if (item.invalid !== 0 && item.selected == '1') {
this.delIdArr.push(item.id)
}
})
console.log(this.delIdArr)
if (this.delIdArr.length < 1) {
tools.showError('请先选择一个商品')
} else {
this.isDelSelect = true
this.confirmShow = true
}
},
_getShopCartData() {
let url = '/Mobile/Cart/index.html'
axios.get(url, {
params: {
}
})
.then((res) => {
console.log(res)
this.allData = res.data.cart
this.countPrice()
})
.catch((error) => {
console.log(error)
})
},
goDedails(id) {
this.$router.push({path: 'equipdetails', query: { id: id }})
}
},
components: {
pubfooter,
Confirm
}
}



